body {
  font-family: 'Open Sans Condensed', sans-serif;
}

.App-logo {
  /*animation: App-logo-spin infinite 20s linear;*/
  height: 50px;
}

.App-header {
  display: block;
  margin: auto;
  width: 120px;
  height: 90px;
  padding: 20px;
  text-align: center;
  font-family: 'Open Sans Condensed', sans-serif;
}

.App-header img {
  float: left;
  margin-top: 16px;
  margin-right: 8px;
}

.App-header h1 {
  float: left;
}

.App-title {
  font-size: 2em;
    font-weight: 100;
    font-variant: small-caps;
}

.App-intro {
  font-size: large;
}

.file-list {
  font-size: 1em;
  margin-top: 6px;
    margin-bottom: 28px;
}

.row {
  margin: 10px 0 ;
}

.max-width {
  max-width: 1024px;
  margin: auto;
}

.ui-widget {
  font-family: 'Open Sans Condensed', sans-serif!important;
  font-weight: 100!important;
}

.ui-card-subtitle, .ui-card-title {
  font-weight: 100!important;
}

.ui-progressbar {
  height: 1.6em!important;
}

.text-right {
  text-align: right;
}


.ui-button.ui-widget {
    font-size: 1.2em;
}

.ui-progressbar-determinate .ui-progressbar-label {
  font-weight: 100!important;
}

.text-center {
  text-align: center;
}

h3 {
  font-weight: 100;
}

.file-uploader-wrapper {
  width: 100%;
  height: 60px;
  position: relative;
}

.file-uploader-wrapper div {
  position: absolute;
  width: 100%;
  height: 100%;
  font-size: 1.2em;
  padding-top: 14px;
}

.file-uploader-wrapper input {
  position: absolute;
  opacity: 0;
  width: 100%;
    height: 100%;
}

.btn-w80px .ui-button {
  width: 80px;
}

.ui-button.ui-button-success:enabled:hover, .ui-button.ui-button-success:focus, .ui-splitbutton.ui-button-success .ui-button:enabled:hover, .ui-splitbutton.ui-button-success .ui-button:focus {
  background-color: #4cae4c;
  border-color: #5cb85c;
}

.minHeight100 {
  min-height: 100%;
}


